body.light-mode {
    background-color: #fff;
    color: #333;
    transition: background-color 0.3s ease;
}

.link-container {
    display: flex;
    justify-content: space-between;
    gap: 0.5rem;
}

a {
    color: white;
}

body.dark-mode {
    background-color: #10171d;
    color: #dfdfdf;

    .dark-mode-toggle > button {
        color: #999;
        &:last-child {
            color: lightblue;
        }
    }
}

.navbar {
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: #25303a;
    padding: 20px;
}

