body.dark-mode .content code {
    background-color: #1b2938;
}

body.dark-mode .content a {
    color: #ee09;
}

body.dark-mode .content button {
    color: #ee09;
}

.dark-mode-toggle {
    display: flex;
    margin-right: 1.5rem;
    & > button {
        font-size: 1.2em;
        background: none;
        border: none;
        color: #ffe600;
        cursor: pointer;
        transition: color 0.3s ease;
        &:last-child {
            color: #666;
        }

        &:focus {
            outline: none;
        }
    }
}

.toggle-control {
    position: relative;
    padding: 0 4px;
    display: flex;
    align-items: center;
}

input[type='checkbox'].dmcheck {
    width: 40px;
    height: 10px;
    background: #555;
    position: relative;
    border-radius: 5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    vertical-align: 2px;
    outline: none;

    &:checked + label {
        left: 30px;
    }

    &:focus-visible {
        outline: solid 2px white;
    }

    & + label {
        display: inline-block;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        transition: all 0.3s ease;
        cursor: pointer;
        position: absolute;
        left: 2px;
        background: #fff;
        opacity: 0.9;
        background-color: #f6f6f6;
    }
}

.main-container {
    display: flex;
    flex-direction: column;
}

.name-container .name-title span {
    background-color: #29e743;
}

body.dark-mode .name-container .name-title {
    font-family: Monaco, Consolas, Lucida Console, monospace;
    font-size: 4.5em;
    color: white;
}

.name-container .name-title {
    font-family: Monaco, Consolas, Lucida Console, monospace;
    font-size: 4.5em;
    color: black;
}

.name-container {
    margin: 5rem 15rem auto 15rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.blink-me {
    animation: blinker 1.25s linear infinite;
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

.tech-container {
    display: grid;
    grid-template-columns: repeat(4, minmax(100px, 1fr));
    grid-template-rows: auto;
    grid-gap: 1rem;
    width: 500px;
    span {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        svg {
            height: auto;
            width: 100px;
        }
    }
}
